/* ==============MEDIA QUERIES (PHONES) */
@media screen and (max-width: 480px){
    .intro {
        flex-direction: column;
        height: 55rem;
        gap: 7rem;
    }
    .i-right {
        transform: scale(0.8);
        left: -4rem;
    }

    .floating-div:nth-of-type(1){
        top: -7rem !important;
        /*right: -8rem !important;*/

    }
    .floating-div:nth-of-type(2){
        left: -0.8rem !important;
        /*right: -1rem !important;*/
    }
}